// Extract global env variables.

const env = {
  NAME: process.env.REACT_APP_NAME,
  NODE_ENV: process.env.NODE_ENV,
  PUBLIC_URL: process.env.PUBLIC_URL,
  TITLE: process.env.REACT_APP_TITLE,
  VERSION: process.env.REACT_APP_VERSION,
}

export default env



